*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

html,
body {
  min-height: 100%;
  position: relative;
}

body {
  font-size: 1rem;
}

.container {
  max-width: 1024px;
  margin: auto;
}

* {
  outline: none;
}

ul,
ol,
li,
p,
.form-modal .review-form__content .epilog,
.way-page__content-desc .way-of ul li,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h3 {
  font-family: Halvar Breitschrift;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 110%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #1B2232;
}

ul {
  list-style: none;
  padding: 0;
}

a {
  text-decoration: none;
}


p,
.form-modal .review-form__content .epilog,
.way-page__content-desc .way-of ul li {
  font-family: Circe;
