  letter-spacing: 0.1em;
  margin-bottom: 10px;
}

@media (max-width: 902px) {
  
  p,
  .form-modal .review-form__content .epilog,
  .way-page__content-desc .way-of ul li {
    font-size: 14px;
  }
}

.breadcrumbs {
  margin-bottom: 24px;
}

.breadcrumbs ul {
  display: flex;
}

.breadcrumbs ul li a, .breadcrumbs ul li span {
  font-family: Circe;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 110%;
  letter-spacing: 0.1em;
  color: #ACB6EA;
}

.breadcrumbs ul li + li {
  margin-left: 5px;
}

.breadcrumbs ul li + li:before {
  content: '/';
  color: #ACB6EA;
  margin-right: 7px;
}

.pagination ul {
  display: flex;
  align-items: center;
}

.pagination ul li {
  font-family: Circe;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 110%;
  letter-spacing: 0.1em;
  color: #FFFFFF;
  display: block;
}

.pagination ul li:not(.active) {
  margin: 0 12px;
}

.pagination ul li a {
  color: inherit;
}

.pagination ul li span {
  color: inherit;
  padding: 7px 13px;
  border: 2px solid #fff;
  display: inline-block;
}

.slick-arrow {
  width: 12px;
  height: 20px;
  padding: 0;
  border: none;
  font-size: 0;
  position: absolute;
  top: calc(50% - 20px);
  background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='10' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.707.293a1 1 0 010 1.414L2.414 9l7.293 7.293a1 1 0 01-1.414 1.414l-8-8a1 1 0 010-1.414l8-8a1 1 0 011.414 0z' fill='%23FFFFFF'/%3E%3C/svg%3E");
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: 0.3s;
  z-index: 3;
}

.slick-arrow:hover {
  opacity: 0.8;
}

.slick-prev {
  left: -30px;
}

@media (max-width: 902px) {
  .slick-prev {
    left: -15px;
  }
}

.slick-next {
  right: -30px;
  transform: rotate(180deg);
}

@media (max-width: 902px) {
  .slick-next {
    right: -15px;
  }
}

.slick-dots {
  display: flex;
  margin-top: 24px;
  justify-content: center;
}

.slick-dots li {
  display: flex;
  align-items: center;
}

.slick-dots li button {
  font-size: 0;
  border: none;
  background: #0968BF;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  padding: 0;
  margin: 0 5px;
}

.slick-dots li.slick-active button {
  background: #FF294F;
}

.hidden {
  opacity: 0;
}

.visible {
  opacity: 1;
}

.mobile {
  display: none;
}

@media (max-width: 1280px) {
  section div[class*="__content"] {
    padding-left: 16px;
