.form-modal {
  background: #162079cc;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 101;
}

.form-modal.active {
  display: flex;
}

.form-modal .review-form {
  background: #FFFFFF;
  border: 5px solid #1B2232;
  box-sizing: border-box;
  max-width: 360px;
  width: 90%;
  padding: 16px;
  filter: drop-shadow(10px 10px 0px #1B2232);
}

.form-modal .review-form .close-btn {
  width: 24px;
  height: 24px;
  display: block;
  margin-left: auto;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.3s;
  cursor: pointer;
}

.form-modal .review-form .close-btn:hover {
  transform: rotate(180deg);
}

.form-modal .review-form__content {
  padding: 0 24px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.form-modal .review-form__content .title {
  font-family: Halvar Breitschrift;
  font-style: normal;
  font-weight: bold;
  line-height: 110%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #1B2232;
  font-size: 18px;
  text-align: center;
  margin-bottom: 16px;
}

.form-modal .review-form__content .epilog {
  color: #1B2232;
  text-align: center;
}

.form-modal .review-form__content .input-group {
  margin: 16px 0 0;
}

.form-modal .review-form__content .input-group .text-input {
  background: #FFFFFF;
  border: 1px solid #1B2232;
  box-sizing: border-box;
  padding: 11px 16px;
  margin-bottom: 24px;
  width: 100%;
  outline: none;
  font-family: Circe;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 110%;
  letter-spacing: 0.1em;
  color: #1B2232;
}

.form-modal .review-form__content .input-group .text-input::placeholder {
  color: #BEBEBE;
}

.form-modal .review-form__content .input-group .text-input:last-child {
  margin-bottom: 0;
}

.form-modal .review-form__content .input-group .form-error {
  position: absolute;
  margin-top: -20px;
  font-family: Circe;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 110%;
  letter-spacing: 0.1em;
  color: #FF294F;
}

.form-modal .review-form__content .form-button {
  margin: 24px 0;
  text-align: center;
}

.form-modal .review-form__content .form-button .submit-btn {
  font-family: Halvar Breitschrift;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 110%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 12px 32px;
  background: #FF294F;
  border: 3px solid #1B2232;
  box-sizing: border-box;
  transition: 0.3s;
  cursor: pointer;
}

.form-modal .review-form__content .form-button .submit-btn:hover {
  background: #0968BF;
}

.form-modal .review-form__content .form-button .submit-btn:active {
  background: #0968BF;
  border-color: #fff;
}

.form-modal .review-form__content .thanks {
  padding: 120px 0;
}

@media (max-width: 480px) {
  .form-modal .review-form__content {
    padding: 0;
  }
}

