  background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='10' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.707.293a1 1 0 010 1.414L2.414 9l7.293 7.293a1 1 0 01-1.414 1.414l-8-8a1 1 0 010-1.414l8-8a1 1 0 011.414 0z' fill='%23FFFFFF'/%3E%3C/svg%3E");
